import React from "react";
import "../App.css";
import { Link, Links } from "react-router-dom";

function Nav() {
  const navStyle = {
    color: "white",
    textDecoration: "none",
  };

  return (
    <nav>
      <h3>Ashan Glenn</h3>
      <ul className="nav-links">
        <Link style={navStyle} to="/about">
          <li>About</li>
        </Link>
        <Link style={navStyle} to="/myworks">
          <li>My Works</li>
        </Link>
        <Link style={navStyle} to="/contactme">
          <li>Contact Me</li>
        </Link>
      </ul>
    </nav>
  );
}

export default Nav;
